// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
    font-size: ($sizeValue * 16) * 1px;
    font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

//archive #primary
@mixin archive-primary {
    width: 1120px;
    padding: 0;
    border: none;
    margin: 0 auto;
}