/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/


/* Buttons */

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    border: none;
    border-radius: 0px;
    background: $color-clicks;
    color: #fff;
    font-family: $font-novecento;
    text-transform: uppercase;
    font-size: 0.9rem;
    line-height: 1;
    padding: .75rem 1.5rem;
    &:hover {
        border-color: #ccc #bbb #aaa;
    }
    &:active,
    &:focus {
        border-color: #aaa #bbb #bbb;
        box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
    }
}


/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
    color: #666;
    background-color: #f4f4f4;
    border: 1px solid #e1e1e1;
    margin-left: 0;
    width: 190px;
    &:focus {
        outline: none;
    }
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
    padding: 3px;
}

textarea {
    padding-left: 3px;
    width: 100%;
}

label {
    color: #2f1339;
    display: block;
    font-family: $font-novecento, sans-serif;
    font-size: 17.6px;
    font-size: 1.1rem;
    margin-bottom: .5rem;
    text-transform: uppercase;
}